/*============== Стили свайпера ===============*/
video {
  object-fit: cover;
  object-position: bottom;
}

.swiper-container-1 {
  position: relative;
  width: 100%;
  height: 100%;
  bottom: 0;
  right: 0;
}

.swiper-wrapper-1 {
  position: relative;
  bottom: calc(var(--um) * 120);
  right: 0;
  aspect-ratio: 502/400;
}

.swiper-buttons-1 {
  min-height: calc(var(--um) * 120);
  display: flex;
  justify-content: space-between;
  align-items: center;
