}

/* ------------------------ */
.sw-container-2 {
  overflow: hidden;
  margin: 0 auto;
  padding-bottom: 50px;
}

.swiper-scrollbar {
  margin-bottom: calc(var(--um) * 120);
}

.sw__buttons {
  display: flex;
  gap: 150px;
  justify-content: center;
  padding-top: calc(var(--um) * 70);
}

