  color: var(--color-orange);
}

.swiper-slide-2 {
  aspect-ratio: 1035/669;
  border-radius: 8px;
  overflow: hidden;
}

.swiper-slide > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-container .swiper-pagination-fraction {
  font-size: max(12px, var(--um) * 20);
  color: #fff;
