  width: 80%;
  position: absolute;
  bottom: calc(var(--um) * 40);
  left: calc(var(--um) * 40);
  transition: opacity 0.3s ease;
  z-index: 1;
}

.card-gradient {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.1) 50%, rgba(0, 0, 0, 0.7) 100%);
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
}

.avatars > img {
  border-radius: 50%;
  border: 3px solid #fff;
  width: 48px;
  height: 48px;
}

.avatars > img + img {
  margin-left: -15px;
}

.inspirations__destination {
  font-size: max(7px, var(--um) * 10);
  line-height: 1.2;
  letter-spacing: 1.1px;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 8px;
}

.inspirations__destination-title {
  font-weight: 700;
  font-size: max(16px, var(--um) * 32);
  line-height: 1.25;
  text-align: left;
  color: #fff;
  margin-bottom: 22px;
}

/* inspirations__card-hover----------------- */
.inspirations__card-hover {
  position: absolute;
  background-color: rgba(20, 27, 35, 0.7);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  text-align: center;
  align-content: center;
  padding: 0 calc(var(--um) * 79);
  opacity: 0;
  cursor: pointer;
  transition: opacity 0.6s ease;
  z-index: 2;
}

.inspirations__card-hover > h3 {
  font-weight: 400;
  font-size: max(17px, var(--um) * 32);
  line-height: 1.25;
  font-family: serif;
}

.inspirations__card-hover > p {
  font-size: max(7px, var(--um) * 10);
  line-height: 1.2;
  letter-spacing: 1.1px;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.inspirations__card-hover:hover {
  opacity: 1;
}

.line {
  max-width: 1920px;
  height: 1px;
  box-shadow: inset 0px 1px 0px rgba(179, 179, 179, 0.5);
}

/* footer -------------- */
.footer {
  margin-top: calc(var(--um) * 124);
  margin-bottom: calc(var(--um) * 121);
}

.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 32px;
  justify-content: space-between;
}

.footer__segment-title {
  position: relative;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 2.15px;
  text-transform: uppercase;
  margin-bottom: 64px;
}

.footer__segment-title::after {
  content: "";
  position: absolute;
  display: block;
  width: 48px;
  height: 1px;
  background-color: #57a773;
  top: 47px;
}

.footer__list > li {
  font-weight: 500;
  font-size: 13px;
  line-height: 29px;
  text-align: left;
  color: #949da5;
  cursor: pointer;
  white-space: nowrap;
}

.footer__list > li:hover {
  color: var(--color-orange);
}

.footer-form {
  display: flex;
  flex-direction: column;
}

.footer-input {
  font-weight: 400;
  font-size: max(9px, var(--um) * 16);
  line-height: 1.19;
  letter-spacing: -0.2px;
  color: #1b1916;
  opacity: 0.5;
  margin-bottom: 16px;
  border: 1px solid #e4e4e4;
  border-radius: calc(var(--um) * 8);
  padding-left: calc(var(--um) * 32);
  width: 100%;
  height: max(40px, var(--um) * 64);
  margin-top: 14px;
}

.footer__form-button {
  background-color: var(--color-blue);
  width: 100%;
  height: max(40px, var(--um) * 64);
}

@media (max-width: 992px) {
  .footer__wrapper {
    grid-template-columns: repeat(3, 30%);
  }
}
@media (max-width: 578px) {
  .footer__wrapper {
    grid-template-columns: repeat(2, 45%);
  }
}
@media (max-width: 380px) {
  .footer__wrapper {
    grid-template-columns: repeat(1, 90%);
  }
}
.privacy-policy {
  display: flex;
  gap: calc(var(--um) * 30);
  justify-content: space-between;
  padding: calc(var(--um) * 32) 0;
  font-weight: 400;
  font-size: max(9px, var(--um) * 13);
  line-height: 1.23;
  color: #959ea6;
}

.privacy-policy > p {
  margin-right: auto;
}

@media (max-width: 578px) {
  .privacy-policy {
    flex-direction: column;
  }
}